import React from 'react';

import {Link} from 'react-router-dom';

class Head extends React.Component {
  renderLinks() {
    if (this.props.admin.email) {
      return (
        <ul className="nav navbar-nav ml-auto">
          <li className="nav-item active">
            <a className="nav-link" href="#">Logout<span className="sr-only">(current)</span></a>
          </li>
        </ul>
      )
    }
  }
  
  render() {
    return (
      <nav className="navbar navbar-expand-lg navbar-light bg-light">
        <Link className="navbar-brand" to="/">
          <img src="https://static1.squarespace.com/static/59ed9589e45a7c27e9400b3c/t/5a241aa5e2c483bcf3cf4287/1541907203250/?format=1500w" width="100" height="60" alt="WakeCap Technologies"/>
        </Link>
        {this.renderLinks()}
      </nav>
    );
  }
}

export default Head;
